// shengqu
// import SwiperBar from '@/components/swiper-bar.vue'; //swiper
<template>
	<div class="SwiperBar">
		  <div class="MoreProductStories">
	 	  	    <div class="MoreProductStories_top clearfix" v-if="title!=''">
	 	  	    	     <h3 class="MoreProductStories_top_title">{{title}}</h3>
	 	  	    	     <a href="javascript:;" class="MoreProductStories_top_more">{{more}}</a>
	 	  	    </div>
          <swiper :options="swiperOption" ref="mySwiper">
					    <swiper-slide v-for="(item,index) in billboardList" class="MoreProductStories_swiper" :key="index">
					    	  <div @click="imgBtn(index)" class="swiper_slide_box">
					    	  	   <a href="javascript:;" class="MoreProductStories_a">
					    	  	     <img src="@/assets/comment-bar/dd.jpg" alt="" class="MoreProductStories_img"/>
					    	     </a>
					    	     <div class="SwiperBar_star_number">
					    	     	  <img src="../assets/comment-bar/in_star_null_icon@3x.png" class="SwiperBar_star" />
					    	     	  <span class="SwiperBar_star_total">1234</span>
					    	     </div>
					    	    <p class="MoreProduct_swiper_imgWord">{{item.name}}</p>
					    	  </div>
					    </swiper-slide>
          </swiper>
	 	  </div>
	</div>
</template>
<style scoped lang="less">
  @m:750/375;
  .swiper_slide_box{
  	  position: relative;
  }
  .SwiperBar_star_number{
  	   position: absolute;
  	   right: 10px*@m;
  	   top: 10px*@m;
  }
  .SwiperBar_star{
  	  width: 12px*@m;
  	  height: 12px*@m;
  	  float: left;
  	  margin-right: 8px*@m;
  }
  .SwiperBar_star_total{
  	  float: left;
  	  font-family: PingFangSC-Regular;
		font-size: 12px*@m;
		color: #FFFFFF;
		letter-spacing: 0;
		line-height: 12px*@m;
  }
  
  .MoreProductStories{
 	  padding: 0 0 0 20px*@m;
 }
 .MoreProductStories_swiper{
 	 width: 240px*@m!important;
 	 display:inline-block;
 	 position: relative;
 	 
 }
 .MoreProductStories_a{
 	 display: block;
 	 width: 100%;
 	 height: 160px*@m;
 }
 .MoreProductStories_img{
 	  width: 100%;height: 100%;
 	  object-fit: cover;	  
 	  border-radius: 8px*@m;
 }
 .MoreProduct_swiper_span{
 	  font-family: PingFangSC-Medium;
		font-size: 12px*@m;
		color: #0F2540;
		letter-spacing: 0;
		line-height: 12px*@m;
		padding: 5px*@m 6px*@m;
		position: absolute;
		left: 10px*@m;
		top: 10px*@m;
		background: #FFFFFF;
    border-radius: 100px;
    text-align: center;
 }
 .MoreProduct_swiper_imgWord{
 	  width: 100%;
 	  font-family: PingFangSC-Medium;
		font-size: 16px*@m;
		color: #0F2540;
		letter-spacing: 0;
		line-height: 18px*@m;
		margin-top: 10px*@m;
 }
 .MoreProduct_swiper_imgName{
 	  font-family: PingFangHK-Medium;
		font-size: 10px*@m;
		color: #B9B9B9;
		letter-spacing: 0;
		line-height: 10px*@m;
		margin-top: 8px*@m;
 }
 .MoreProductStories_top{
 	 width: 100%;
 	 margin: 30px*@m 0 20px*@m 0;
 }
 .MoreProductStories_top_title{
    float: left;
    font-family: PingFangSC-Medium;
		font-size: 14px*@m;
		color: #0F2540;
		letter-spacing: 0;
		line-height: 14px*@m;
  }
  .MoreProductStories_top_more{
  	   font-size: 14px*@m;line-height: 14px*@m;
  	   float: right;
  	   margin-right: 20px*@m;
  	   color: #B9B9B9;
  	   text-decoration: none;
  }
  
</style>
<script>
export default {
	props: ['billboardList','imgTip','title','more'],
  data () {
    return {
    	   swiperOption: {
			    slidesPerView: 1,
					slidesPerView: 'auto',
			    spaceBetween: 40,
			    slidesPerGroup: 1,
			    loop: true,
			    loopFillGroupWithBlank: true,
			  }
    }
  },
  methods:{
  	   imgBtn(index){
       	  this.$emit('imgClick',index);
  	   }
  },
  computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      }
  },
  mounted(){
  	   
  }

}
</script>


