// shengqu
// import SwiperBar from '@/components/swiper-bar.vue'; //swiper
<template>
	<div class="SwiperBar">
		  <div class="MoreProductStories">
          <swiper :options="swiperOption" ref="mySwiper">
					    <swiper-slide v-for="(item,index) in swiperDataList" class="MoreProductStories_swiper" :key="index">
					    	   <div>
					    	   	  <a href="javascript:;" class="MoreProductStories_a">
					    	    	    <img  src="@/assets/comment-bar/dd.jpg" alt="" class="MoreProductStories_img"/>
					    	      </a>
					    	      <p class="MoreProductStories_imgName">{{item.name}}</p>
					    	   </div>
					    </swiper-slide>
          </swiper>
	 	  </div>
	</div>
</template>
<style scoped lang="less">
  @m:750/375;
  .MoreProductStories{
 	  
 }
 .MoreProductStories_swiper{
 	 width: 120px*@m!important;
 	 display:inline-block;
 	 position: relative;
 	 height: 136px*@m;
 	 background: #FFFFFF;
   box-shadow: 0 0 30px*@m 0 rgba(227,227,227,0.80);
   border-radius: 8px*@m;
   overflow: hidden;
 }
 .MoreProductStories_a{
 	  width: 100%;
 	  height:100px*@m;
 	  display: block;
 }
 .MoreProductStories_img{
 	 width: 100%;height: 100%;
 	 object-fit: cover;
 }
 .MoreProductStories_imgName{
 	 width: 100%;
 	 text-align: center;
 	 font-family: PingFangSC-Regular;
	font-size: 14px*@m;
	color: #0F2540;
	letter-spacing: 0;
	text-align: center;
	line-height: 16px*@m;
	margin-top:10px*@m;
 }

  
</style>
<script>
export default {
	props: ['swiperDataList','imgTip','title','more'],
  data () {
    return {
    	   swiperOption: {
			    slidesPerView: 1,
					slidesPerView: 'auto',
			    spaceBetween: 32,
			    slidesPerGroup: 1,
			    loop: true,
			    loopFillGroupWithBlank: true,
			  }
    }
  },
  methods:{
  	   imgBtn(index){
//     	  this.$emit('imgClick',index);
         cosole.log(index);
  	   }
  },
  computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      }
  },
  mounted(){
  	   
  }

}
</script>


